import * as React from 'react'
import { ResumeProps } from '../types'

interface DataProps {
  data: ResumeProps
}

export const Resume: React.FunctionComponent<DataProps> = (props) => {
  const education = props.data.education.map((education) => {
    return (
      <div key={education.school}>
        <h3>{education.school}</h3>
        <p className="info">
          {education.degree} <span>&bull;</span>
          <em className="date">{education.graduated}</em>
        </p>
        <p>{education.description}</p>
      </div>
    )
  })
  const work = props.data.work.map((work) => {
    const descriptions = work.description.split('$')
    return (
      <div key={work.company}>
        <h3>{work.company}</h3>
        <p className="info">
          {work.title}
          <span>&bull;</span> <em className="date">{work.years}</em>
        </p>
        <ul>
          {descriptions.map((description) => (
            <li>{description} </li>
          ))}
        </ul>
      </div>
    )
  })
  const skills = props.data.skills.map((skills) => {
    const className = 'bar-expand ' + skills.name.toLowerCase()
    return (
      <li key={skills.name}>
        <span style={{ width: skills.level }} className={className} />
        <em>{skills.name}</em>
      </li>
    )
  })

  return (
    <section id="resume">
      <div className="row education">
        <div className="three columns header-col">
          <h1>
            <span>Education</span>
          </h1>
        </div>

        <div className="nine columns main-col">
          <div className="row item">
            <div className="twelve columns">{education}</div>
          </div>
        </div>
      </div>

      <div className="row work">
        <div className="three columns header-col">
          <h1>
            <span>Work</span>
          </h1>
        </div>

        <div className="nine columns main-col">{work}</div>
      </div>

      <div className="row skill">
        <div className="three columns header-col">
          <h1>
            <span>Skills</span>
          </h1>
        </div>

        <div className="nine columns main-col">
          <div className="bars">
            <ul className="skills">{skills}</ul>
          </div>
        </div>
      </div>
    </section>
  )
}



